<template>
  <div class="edit-style">

    <div class="bed-desc">
      <img class="bed-img" src="../assets/logo.png"/>
      <div class="desc">
        <p class="price">$699</p>
        <p class="remain">库存：7</p>
        <p class="choice-style">请选择 款式 尺寸</p>
      </div>
    </div>

    <div class="style-template union-id-1">
      <div class="title">款式</div>
      <div class="styles">
        <p class="style" v-for="(li, index) in kinds" :key="index" :class="{'zero-style': li[0].total === 0}" @click="changeKind(li, $event)">{{li[0].kind_style}}</p>
        <!-- <p class="style" :class="{'zero-style': true}" @click="changeKind('春日气息：绿色', $event)">春日气息：绿色</p>
        <p class="style" :class="{'zero-style': true}" @click="changeKind('阳光记忆：米黄', $event)">阳光记忆：米黄</p>
        <p class="style" :class="{'zero-style': true}" @click="changeKind('静晓：天蓝', $event)">静晓：天蓝</p> -->
      </div>
    </div>

    <div class="size-template style-template" v-if="nowKind.length >= 1">
      <div class="title">尺寸</div>
      <div class="sizes styles">
        <p class="size style" v-for="(li, index) in nowKind" @click="selectMethod(li, $event)" :class="{'zero-style': li.total <= 0}" :key="index">{{ li.kind_size }}</p>
      </div>
    </div>

    <div class="count-template style-template" v-if="select">
      <div class="title">数量</div>
      <div class="counter-template" v-if="select">
        <div class="outer">
          <div class="minus" @click="minus">-</div>
          <div class="csp" v-if="minusDisable"></div>
        </div>
        <div class="number">{{ acceptNumber }}</div>
        <div class="outer">
          <div class="add" @click="add">+</div>
          <div class="csp" v-if="addDisable"></div>
        </div>
      </div>
    </div>

    <div class="button" @click="submitData" :class="{'background-blue': acceptNumber > 0}">确定</div>
  </div>
</template>

<script>

export default {
  name: 'editStyle',
  props: {
    msg: String
  },
  data () {
    return {
      minusDisable: true,
      addDisable: false,
      acceptNumber: 0, // 选择中的数量
      select: '', // 已选中的
      nowKind: [], // 当前点击选中的kind
      kinds: {}
    }
  },
  methods: {
    minus () {
      // eslint-disable-next-line
      this.acceptNumber > 1 ? this.acceptNumber -= 1 : ''
    },
    add () {
      // eslint-disable-next-line
      this.acceptNumber < +this.select.kind_total_count ? this.acceptNumber += 1 : ''
    },
    // 选择最终结果
    selectMethod (li, e) {
      if (li.total <= 0) {
        return
      }
      document.querySelectorAll('.size-template .styles .style').forEach(v => {
        v.classList.remove('select-style')
      })
      e.srcElement.classList.add('select-style')
      this.select = li
      this.acceptNumber = 0 // 初始化 0 在这里刷新
      this.$nextTick(() => {
        this.minusDisable = true
        this.addDisable = false
      })
    },
    // 选择当前样式
    changeKind (li, e) {
      if (li[0].total <= 0) {
        return
      }
      this.nowKind = []
      this.select = null // 初始化
      document.querySelectorAll('.size-template .styles .style').forEach(v => {
        v.classList.remove('select-style')
      })
      document.querySelectorAll('.union-id-1 .styles .style').forEach(v => {
        v.classList.remove('select-style')
      })
      e.srcElement.classList.add('select-style')
      this.nowKind = li
    },
    // 最终选中提交结果
    submitData () {
      if (this.acceptNumber <= 0) {
        return
      }
      this.$emit('submitData', {
        count: this.acceptNumber,
        data: this.select
      })
      // eslint-disable-next-line
      this.$parent && this.$parent.hideGreenTemplate
        ? this.$parent.hideGreenTemplate()
        : ''
    }
  },
  watch: {
    acceptNumber (val, oldVal) {
      // console.log(this.select);
      (this.acceptNumber <= +this.select.kind_total_count && this.acceptNumber !== 1) ? this.minusDisable = false : this.minusDisable = true;
      (this.acceptNumber >= 1 && this.acceptNumber !== +this.select.kind_total_count) ? this.addDisable = false : this.addDisable = true
      // this.inputData.count = val
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped src="./editStyle.scss">
</style>
