<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import editStyle from '../components/editStyle' // 用户选择款式

// [[]] 如果没有 v 那么就push一个空的进去 我记起来是用concat的 [].concat([v])
// [[v]] 查看有没有 v, 如果有[[v, v]], 如果没有 [[v], [v2]]

export default {
  name: 'home',
  data () {
    return {
      greenTemplate: false, // 展示灰色背景
      editStyle: false, // 是否展示选择样式页
      nowShowInGreenTemplate: [], // greenTemplate 中 显示的组件的名称
      contentType: 1, // 中间bar的 三个选项 展示的内容开关
      home_height: 0,
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      },
      selectGoods: null // 最终选中的商品
    }
  },
  components: {
    swiper,
    swiperSlide,
    editStyle
  },
  methods: {
    // editStyle 组件中收取到的
    acceptData (data) {
      this.selectGoods = data
    },
    // 关闭 greenTemplate
    hideGreenTemplate () {
      this.greenTemplate = false
      this.nowShowInGreenTemplate.forEach(v => { this[v] = false })
    },
    // 展示 greenTemplate
    showGreenTemplate (name, boolean) {
      this.greenTemplate = true
      this[name] = boolean
    },
    // 获取食物商品款式
    getGoodsKind () {
      this.$axios.get('/web/v1/cosleep/entitygoods/kind', { params: { goods_id: this.$root.goods_id } }).then(res => {
        if (res.data.status !== 1) {
          // console.log('网络错误')
          return
        }
        let kinds = {
          '春日气息：绿色': [],
          '阳光记忆：米黄': [],
          '静晓：天蓝': []
        }
        const data = res.data.data
        data.forEach(v => {
          kinds[v.kind_style].push(v)
        })

        for (let i in kinds) {
          let total = 0
          kinds[i].forEach(v => {
            total += +v.kind_total_count
          })
          kinds[i].forEach(v => {
            v.total = total
          })
        }

        // console.log(this.$refs['editStyle'])
        this.$refs['editStyle'].kinds = kinds
      })
    },
    // 立刻下单
    submit (boolean) {
      if (boolean) {
        this.$router.push(`/edit?id=${this.selectGoods.data.kind_id}&count=${this.selectGoods.count}`)
      }
    },
    contentTypeChange (type) {
      this.contentType = +type
    }
  },
  mounted () {
    this.getGoodsKind()
  }
}
</script>

