<template>
  <div class="edit">
    <div class="user-area border-bottom-white" @click="addressShow = true">
      <p class="area" v-if="inputData === null">请填写配送信息</p>
      <div v-else>
        <p class="user">{{`${inputData.receiver_name} ${inputData.mobile}`}}</p>
        <p class="user-area-p">{{`${inputData.province}-${inputData.city}-${inputData.area}-${inputData.address}`}}</p>
      </div>
      <p class="right-icon">></p>
    </div>

    <div class="pay-way border-bottom-white">
      <div class="weixin"></div>
      <div class="ali"></div>
    </div>

    <div class="goods-template border-bottom-white">
      <img class="goods-img" src="../assets/logo.png"/>
      <div class="goods-desc" v-if="goods">
        <p class="name">小睡眠豪华舒适四件套</p>
        <div class="desc">{{goods.kind_style}}<span class="count">x{{count}}</span></div>
        <p class="price">￥{{goods.kind_total_count}}</p>
      </div>
    </div>

    <div class="common-template-evenly border-bottom-white">
      <p class="desc">商品金额</p>
      <p class="price" v-if="goods" style="font-family: 'br'">￥{{goods.kind_total_count * count}}</p>
    </div>
    <div class="common-template-evenly">
      <p class="desc">运费</p>
      <p class="price" style="color: #000">免邮</p>
    </div>

    <div class="meain-pad-color"></div>

    <div class="button-template">
      <div class="icon-template" @click="back">
        <img class="icon" src="../assets/icon/order.png"/>
        取消
      </div>
      <p class="price" v-if="goods">￥{{goods.kind_total_count * count}}</p>
      <div class="button" @click="createAOrder" :class="{'background-blue': inputData}">前往支付</div>
    </div>

    <Address v-show="addressShow" class="address" />
  </div>
</template>

<script>
import Address from '../components/usersAddress.vue'

export default {
  name: 'editBuyPage',
  data () {
    return {
      // 填写地址的模板是否展示 广东省广州市海珠区新港东路1226号C塔14层
      addressShow: false,
      goods: null,
      count: null,
      inputData: null
    }
  },
  components: {
    Address
  },
  methods: {
    createAOrder () {
      const data = {
        kind_id: this.goods.kind_id,
        order_goods_count: this.count,
        order_user_name: this.inputData.receiver_name,
        order_user_mobile: this.inputData.mobile,
        order_address: this.inputData.address
      }
      this.$axios({
        url: '/web/v1/cosleep/entitygoods/order',
        method: 'POST',
        data,
        headers: {
          'openid': this.$root.openid || 'b389419558def31fd0adf7b1dd6b7ec4'
        }
      }).then(res => {
        const status = res.data.status
        if (status === 1) {
          this.$router.push('/paypage')
        }
      })
    },
    back () {
      this.$router.go(-1)
    }
  },
  mounted () {
    const id = +this.$route.query.id
    const count = +this.$route.query.count
    if (id && count) {
      this.count = count
      this.$axios.get('/web/v1/cosleep/entitygoods/kind', { params: { goods_id: this.$root.goods_id } }).then(res => {
        if (res.data.status !== 1) {
          // console.log('网络错误')
          return
        }
        const data = res.data.data
        for (let i of data) {
          if (i.kind_id === id) {
            this.goods = i
            return
          }
        }
      })
    } else {
      // this.$router.push('/')
    }
  }
}
</script>

<style lang="scss" scoped src="./editBuyPage.scss">
</style>
